a{text-decoration: none;}
.loginForm{
  border:none;
  width: 500px; margin: 0 auto;
}
.loginForm legend{font-size: 24px; font-weight: bold; text-align: center;}
.loginForm label{display: none;}
.loginForm input{display: block; width: 100%; padding: 14px 4px; margin: 20px 0 5px 0;}
.loginForm p{text-align: right; color: #808080;}
.loginForm p a{color: #808080;}
.loginBtn, .joinBtn{font-size: 16px; cursor: pointer;;}
.loginForm .loginBtn{background: #600080; color: #fff;}
.loginForm .joinBtn{background: #fff; border: 1px solid #600080;}
.idPw{text-align: right;}