body{max-width: 1200px; margin: 0 auto; }
*{padding: 0; color: #333;}
li{list-style-type: none;}

header{margin: 20px 0;}
header img{width: 100px;}
.md_reco h2{text-align: center; margin: 30px 0;}
.md_reco ul{
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
.md_reco ul li{
  background-color: #f1f1f1;
  padding: 8px 16px; border-radius: 50px;
  margin: 10px; cursor: pointer;
}
.md_reco ul li:hover{
  color: #fff;
  background-color: #600080;
}

.prod_img{margin-top: 30px;}
.prod_img h2{display: none;}
.prod_img ul{
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}
.prod_img ul li{width: 270px; margin-bottom: 20px;}
.prod_img ul li img{width: 100%;}
.prod_img ul li p{
  font-size: 16px; color: #333; line-height: 10px;
}
.prod_img ul li p:nth-of-type(2){font-weight: bold;}
.prod_img ul li p span{
  color: #fa622f; margin-right: 10px;
}
.prod_img ul li p:nth-of-type(3){font-size: 14px; color: #b5b5b5; text-decoration: line-through;}